@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  line-height: 1.6;
  background-color: #6EC5E9;
}

h1 {
  font-size: 30px;
  font-weight: 700;
}

h2 {
  font-size: 24px;
  font-weight: 700;
}

h3 {
  font-size: 18px;
  font-weight: 700;
}

a {
  color: #202840;
}

a:visited {
  /* visited link */
  color: #202840;
}

a:hover,
a:focus {
  /* No decoration when hovering over a link */
  text-decoration: none;
}

a:active {
  /* selected link */
  color: #489c79;
}

body {
  background-color: #6EC5E9;
}

button,
.button {
  border: none;
  font-family: inherit;
  background-color: #FF5959;
  color: #fff;
  text-decoration: none;
  padding: 10px;
  border-radius: 3px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.3);
  transition: 0.2s opacity;
}

button:hover,
button:focus,
.button:hover,
.button:focus {
  cursor: pointer;
  opacity: 0.8;
}

.test { 
  color: red;
}
.profile {
  max-width: 700px;
  margin: 0 auto;
}

.home-btns {
  margin-bottom: 50px;
}

.profile_portrait {
  float: left;
  width: 250px;
  margin-right: 40px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.profile h1 {
  color: black;
}

.profile p {
  color: black;
  margin-bottom: 50px;
}

.home_button {
  border-radius: 7px;
  padding: 15px 25px;
  color: white;
  margin-left: 5px;
}

a.home_button {
  color: white;
}

a.home_button:visited {
  color: white;
}

@media all and (max-width: 750px) {
  
  .profile {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .profile_portrait {
    margin: 10px 0;
  }
}
/* Colors Used

Text Color (Dark Gray): #2b2b2b;
Primary Color (Purple): #357b70;
Secondary Color (Orange): #e0b354;
*/
